
.navWrapper {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden; 
  padding-left: 20px;
  padding-right: 40px;
  box-sizing: content-box;

  .arrow-next ,.arrow-prev {
    position: absolute;
    top: -2px;
    z-index:10;
    cursor: pointer;
    display: inline-block;
    width: 20px;
    height: 54px;
    line-height: 54px;
    text-align: center;
    background-color: #fff;
  }

  .arrow-prev {
    left: 0;
  }

  .arrow-next {
    right: -6px;
  }

}
.nav-tabs {
  margin-bottom: 15px
}

.nav-scroll {
  max-width: 99999px;
  overflow: hidden;
}



